.container {
  width: 1200px;
  margin: 0 auto;
  padding-right: 24px;
  padding-left: 24px;
}

.header {
  background-color: #422ed4;
}

.logo-container {
  display: flex;
  align-items: center;
}

.logo-container__text {
  font-family: Raleway;
  font-weight: 800;
  font-size: 16px;
  line-height: calc(19 / 16);
  color: #ffffff;
}

.title-categories {
  font-family: 'Raleway';
  text-align: center;
  font-weight: 800;
  font-size: 50px;
  line-height: calc(59 / 50);
  margin-bottom: 69px;
}

.section {
  padding-top: 60px;
  min-height: 850px;
  position: relative;
}

.categories-button {
  padding: 19px 16px;
  border-radius: 20px;
  border: transparent;
  width: 170px;
  cursor: pointer;
}

.categories-button:hover, .categories-button:focus {
  background-color: #422ed4;
  color: #ffffff;
}

.categories-button--text {
  font-family: 'Raleway';
  font-weight: 800;
  font-size: 16px;
  line-height: calc(19 / 16);
  text-align: center;
  color: #1c1c39;
}

.categories-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin-bottom: 165px;
}

.categories-list__item {
  flex: 0 0 calc(16.66% - 20px);
  padding: 10px;
  margin: 5px;
}

.backdrop {
  position: absolute;
  bottom: 0;
  width: 100%;
}

.modal {
  position: relative;
  background: #422ed4;
  max-width: 1120px;
  height: 180px;
  box-shadow: 0px 100px 80px rgba(80, 76, 103, 0.07), 0px 0px 10.0172px rgba(80, 76, 103, 0.035);
  border-radius: 25px;
  padding: 24px;
}

.modal__text {
  font-family: 'Raleway';
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: calc(30 / 20);
  color: #ffffff;
}

.modal__image {
  position: absolute;
  top: -135px;
  right: 20px;
}

.eror-text {
  text-align: center;
}

.not-found-wrap {
  display: flex;
  justify-content: center;
  margin-bottom: 20px;
}

.loader {
  display: flex;
  align-items: center;
  justify-content: center;
}
